import { useState, useEffect } from "react";

export default function Contact() {
  const [showContact, setShowContact] = useState(false);

  // Erkennt Scrollen und versteckt Kontakt
  useEffect(() => {
    const handleScroll = () => {
      if (showContact) {
        setShowContact(false);
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, [showContact]);

  return (
    <>
      {/* Contact Me Button - Platziere ihn in deinem Hero-Bereich */}
      <button
        onClick={() => setShowContact(true)}
        className="px-6 py-3 bg-white text-black font-bold rounded-lg hover:bg-gray-200 transition"
      >
        Contact Me
      </button>

      {/* Kontakt-Overlay */}
      {showContact && (
        <div className="fixed inset-0 bg-black bg-opacity-95 z-50 flex items-center justify-center animate-fadeIn">
          <div className="max-w-2xl mx-auto text-center px-4">
            <h1 className="text-5xl font-bold text-white mb-8">Kontakt</h1>
            <p className="text-gray-400 mb-12">
              Wenn you want to get in touch, feel free to reach out via email at
            </p>
            <a
              href="mailto:einar.jaegereinar@icloud.com"
              className="text-2xl text-blue-400 hover:text-blue-300 transition"
            >
              einar.jaegereinar@icloud.com
            </a>
            <p className="text-gray-500 mt-4 text-sm">
              I typically respond within 24-48 hours.
            </p>
            <button
              onClick={() => setShowContact(false)}
              className="mt-8 px-6 py-2 border border-white text-white rounded hover:bg-white hover:text-black transition"
            >
              Close
            </button>
          </div>
        </div>
      )}
    </>
  );
}
